* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  background: linear-gradient(to right, rgb(236, 168, 168), rgb(240, 240, 96));
  font-family: "Noto Sans TC", sans-serif;
}

.wrapper {
  max-width: 480px;
  margin: 1rem auto;
}

header {
  display: flex;
  justify-content: center;
}

form.inputMessage {
  display: flex;
  flex-direction: column;
  margin-bottom: 3rem;
}
form.inputMessage input {
  margin-bottom: 0.5rem;
  border: 1px solid rgba(0, 0, 0, 0.125);
  border-radius: 0.5rem;
  padding: 0.5rem;
  outline: 0;
}
form.inputMessage textarea {
  font-size: 1rem;
  width: 100%;
  margin-bottom: 0.5rem;
  border: 1px solid rgba(0, 0, 0, 0.125);
  border-radius: 0.5rem;
  padding: 0.5rem;
  outline: 0;
}
form.inputMessage button {
  align-self: flex-end;
  padding: 0.4rem 0.5rem;
  border: 0;
  border-radius: 3px;
  background-color: #3397cf;
  color: white;
  cursor: pointer;
}
form.inputMessage button:hover {
  background-color: #5ab0db;
  color: white;
  transition: all 0.3s ease;
}

main .item {
  border-radius: 0.5rem;
  margin-bottom: 1rem;
}
main .item .message {
  padding: 0.5rem;
  border-radius: 0.5rem 0.5rem 0 0;
  background-color: white;
  display: flex;
  justify-content: space-between;
}
main .item .message .d-flex {
  display: flex;
}
main .item .message .d-flex img {
  margin-right: 0.5rem;
  width: 2.5rem;
  height: 2.5rem;
}
main .item .message .d-flex .msgText h3 {
  margin-top: 0.5rem;
}
main .item .message .deleteBtn button {
  align-self: flex-end;
  padding: 0.4rem 0.5rem;
  border: 0;
  border-radius: 3px;
  background-color: #3397cf;
  color: white;
  cursor: pointer;
}
main .item .message .deleteBtn button:hover {
  background-color: #5ab0db;
  color: white;
  transition: all 0.3s ease;
}
main .item .reply {
  padding: 0.5rem;
  padding-left: calc(40px + 1rem);
  border-radius: 0 0 0.5rem 0.5rem;
  background-color: #f3f4f4;
}
main .item .reply .replyText {
  margin: 0.5rem 0;
  display: flex;
  justify-content: space-between;
}
main .item .reply .replyText .deleteBtn {
  margin-right: 2.5rem;
  cursor: pointer;
}
main .item .reply form input {
  width: 80%;
  padding: 0.5rem;
  margin: 0.5rem 0.5rem 0.5rem 0;
  border: 1px solid rgb(217, 217, 217);
  border-radius: 5px;
  outline: 0;
}
main .item .reply form button {
  padding: 0.4rem 0.5rem;
  border: 0;
  border-radius: 3px;
  background-color: #f39c12;
  color: white;
  cursor: pointer;
}
main .item .reply form button:hover {
  background-color: #f87000;
  color: white;
  transition: all 0.3s ease;
}/*# sourceMappingURL=style.css.map */